    watch: {
        $route: {
            immediate: true,
            handler: function (_route) {
                this.view = _route.name;
            },
        },
    },
    computed: {},
    methods: {
        changeView: function () {
            this.$router.push({
                name: this.view,
                query: {
                    fb_name: this.$route.query.fb_name,
                    fb_zlp: this.$route.query.fb_zlp,
                },
            });
